@import "../base/variables";
@import "../base/mixin";

.remodal-overlay {
  background: rgba(0, 0, 0, 0.2);
}

.remodal-wrapper {
  padding: 10px 20px 0;
}

.donation-remodal {
  max-width: 980px;
  border-radius: 10px;
  text-align: left;

  @include sp {
    padding: 55px 30px 40px;
  }

  @include tabpc {
    padding: 80px 100px 60px 70px;
  }

  &__close {
    top: 10px;
    left: inherit;
    right: 10px;
  }

  &__row {
    display: flex;

    @include sp {
      flex-flow: column-reverse;
    }

    @include tabpc {
    }
  }

  &__detalis {
    @include sp {
      flex-flow: column-reverse;
    }

    @include tabpc {
      width: calc(100% - 368px);
      margin-top: 20px;
      padding-left: 47px;
    }
  }

  &__catch {
    color: $thm_color;
    font-weight: bold;

    @include sp {
      font-size: 1.8rem;
    }

    @include tabpc {
      font-size: 2rem;
    }
  }

  &__ttl {
    position: relative;
    margin-top: 5px;
    padding-bottom: 10px;

    &::before {
      left: 0;
      bottom: -13px;
      content: "";
      position: absolute;
      width: 39px;
      height: 1px;
      background-color: $thm_color;
    }

    color: $black;

    @include sp {
      font-size: 2.9rem;
    }

    @include tabpc {
      font-size: 2.6rem;
    }
  }

  &__txt {
    line-height: 2;

    @include sp {
      margin-top: 35px;
    }

    @include tabpc {
      margin-top: 40px;
    }
  }

  &__pic {
    text-align: center;

    @include sp {
      margin-top: 10px;
    }

    @include tabpc {
      width: 368px;
    }
  }

  &__cv {
    margin-top: 30px;
    text-align: center;
  }
}

.remodal-movie {
  max-width: 980px;
  border-radius: 10px;
  text-align: left;

  @include sp {
    padding: 55px 30px 40px;
  }

  @include tabpc {
    padding: 60px 60px;
  }

  &__close {
    top: 10px;
    left: inherit;
    right: 10px;
  }

  &__row {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;

    iframe,
    object,
    embed {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
}

.c-module01 {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid $border_color01;

  @include sp {
    min-height: 154px;
    background: url(../images/common/bg_hero_main_img01_sp.png) center
      center/cover no-repeat;
  }

  @include tabpc {
    min-height: 170px;
    background: url(../images/common/bg_hero_main_img01.png) center center/cover
      no-repeat;
  }

  &__ttl {
    display: flex;
    margin-top: -30px;
    font-family: $font-rounded-1c;
    color: #000;
    text-align: center;
    line-height: 1.5;
    font-weight: bold;

    @include sp {
      flex-flow: column-reverse;
      font-size: 2.5rem;
      padding: 0 20px;
    }

    @include tabpc {
      flex-flow: column;
      font-size: 3rem;
    }
  }

  &__ttl-en {
    font-family: $font-rounded-1c;
    color: $thm_color;
    font-size: 1.4rem;
    text-align: center;
  }
}

.c-module01-2 {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid $border_color01;

  @include sp {
    min-height: 154px;
    background: url(../images/common/bg_hero_main_img03_sp.png) center
      center/cover no-repeat;
  }

  @include tabpc {
    min-height: 170px;
    background: url(../images/common/bg_hero_main_img03.png) center center/cover
      no-repeat;
  }

  &__ttl {
    display: flex;
    margin-top: -30px;
    font-family: $font-rounded-1c;
    color: #000;
    text-align: center;
    line-height: 1.5;
    font-weight: bold;
    position: relative;
    z-index: 3;

    @include sp {
      flex-flow: column-reverse;
      font-size: 2.5rem;
      padding: 0 20px;
    }

    @include tabpc {
      flex-flow: column;
      font-size: 3rem;
    }
  }

  &__ttl-en {
    font-family: $font-rounded-1c;
    color: $thm_color;
    font-size: 1.4rem;
    text-align: center;
  }

  &.is-our-business {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff2d5;
      opacity: 0.7;
      z-index: 2;
    }
  }
}

.c-module02 {
  position: relative;
  z-index: 2;
  top: -34px;
  background-color: $cl_green02;
  border: 1px solid $border_color01;
  border-left: none;

  @include sp {
    display: block;
    margin-right: 10px;
    padding: 15px 50px 15px 10px;
  }

  @include tabpc {
    display: inline-block;
    padding: 20px 55px 20px calc((100% - 980px) / 2);
  }

  &__inner {
    max-width: 980px;
    margin: 0 auto;
  }

  &__breadcrumb {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__breadcrumb-item {
    position: relative;
    font-size: 1.4rem;

    @include sp {
      font-size: 1.3rem;
    }

    @include tabpc {
      font-size: 1.4rem;
    }

    &:first-child {
      padding-left: 24px;

      &::before {
        position: absolute;
        content: "";
        left: 0;
        top: 42%;
        transform: translateY(-50%);
        width: 17px;
        height: 16px;
        background: url(../images/common/icn_breadcrumb_home_img01.svg) 0 0/16px
          auto no-repeat;
      }
    }

    a {
      &:hover {
        text-decoration: underline;
      }
    }

    &:nth-child(n + 2) {
      margin-left: 8px;
      padding-left: 15px;

      &::before {
        position: absolute;
        content: "";
        left: -5px;
        top: 42%;
        transform: rotate(45deg) translateY(-50%);
        width: 8px;
        height: 8px;
        border-top: 2px solid #b4d883;
        border-right: 2px solid #b4d883;
      }
    }
  }
}

.c-module03 {
  position: relative;
  z-index: 1;
  text-align: center;
  font-weight: bolder;
  font-family: $font-rounded-1c;
  color: $thm_color;

  @include sp {
    font-size: 3.9rem;
  }

  @include tabpc {
    font-size: 4.6rem;
  }
}

.c-module04 {
