.c-module07 {
  position: relative;
  z-index: 1;
  background-color: $bg_color01;
  padding: 50px 20px;

  &__inner {
    max-width: 737px;
    margin: 0 auto;
  }

  &__links-list {
    @include tabpc {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }

  &__links-item {
    @include sp {
      &:not(:first-child) {
        margin-top: 15px;
      }
    }

    @include tabpc {
      width: 48%;

      // &:nth-child(1) {
      //   .c-module07__links-pic {
      //     margin-top: -5px;
      //   }
      // }
      // &:nth-child(4) {
      //   .c-module07__links-pic {
      //     padding-top: 10px;
      //   }
      // }
      &:nth-child(n + 3) {
        margin-top: 30px;
      }
    }
  }

  &__links-link {
    position: relative;
    border-radius: 5px;
    box-shadow: 0 0 20px rgba(188, 211, 172, 0.2);
    background-color: #fff;
    transition: box-shadow 0.4s;

    @include sp {
      display: flex;
      padding: 30px 20px;
      max-height: 150px;
      align-items: center;
      justify-content: center;

      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 54px;
        height: 54px;
        background: url(../images/common/icn_main_arrow_img08.svg) 0 0/54px auto
          no-repeat;
      }
    }

    @include tabpc {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px 30px 25px;
      height: 100%;

      &:hover {
        // box-shadow: 0 0 15px rgba(124, 114, 87, 0.25);
        box-shadow: 0 0 10px #ffc62f;

        img {
          transform: translateY(-3px) scale(1.02);
        }
      }
    }
  }

  &__links-wrap {
    @include sp {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
    }

    @include tabpc {
      text-align: center;
    }
  }

  &__links-pic {
    position: relative;
    z-index: 2;

    @include sp {
      width: 27%;
      max-width: 120px;
      text-align: center;
    }

    @include tabpc {
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 129px;
    }

    img {
      @include sp {
        max-height: 95px;
      }

      transition: all 0.4s !important;
    }
  }

  &__links-detalis {
    @include sp {
      padding-left: 10px;
      min-width: 63%;
    }
  }

  &__links-catch {
    color: $thm_color;
    font-family: $font-rounded-1c;
    font-size: 1.6rem;
  }

  &__links-ttl {
    font-family: $font-rounded-1c;
    line-height: 1.6;
    color: #000;
    font-weight: bold;

    @include sp {
      font-size: 2.4rem;
      line-height: 1.4;
    }

    @include tabpc {
      font-size: 2.6rem;
    }
  }
}

