.c-module13 {
  position: relative;
  margin-top: 74px;
  padding: 60px 0;
  z-index: 1;
  // overflow: hidden;
  background-color: $cl_orange02;

  @include tabpc {
    margin-top: -40px;
    padding-top: 40px;
  }

  &::after {
    content: "";
    position: absolute;
    top: -70px;
    right: 0;
    width: 209px;
    height: 148px;
    background-image: url(../images/challenges/img_step_illust.png);
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__wrap {
    position: relative;
    z-index: 1;

    @include sp {
      padding-bottom: 120px;

      &::after {
        content: "";
        position: absolute;
        z-index: 0;
        width: 100%;
        height: 81px;
        left: 0;
        bottom: 0;
        background: url(../images/challenges/icn_step_line_img02.svg) center 0
          no-repeat;
      }
    }

