.c-module16 {
  position: relative;
  z-index: 1;
  // @include tabpc {
  //   padding: 0 40px;
  // }

  &__inner {
    background-color: $bg_color02;

    @include sp {
      padding: 80px 0 40px;
    }

    @include tabpc {
      min-width: 1131px;
      margin: 0 auto;
      padding: 80px 0 60px;
    }
  }

  &__slider {
    @include sp {
      margin-top: 35px;
      min-height: 530px;

      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 120px;
        left: 0;
        z-index: 0;
        background: url(../images/history/bg_staff_slider_img01.svg) center 0 /
          cover no-repeat;
      }

      .slick-track {
        padding-top: 70px;
      }
    }

    @include tabpc {
      max-width: 1131px;
      margin: 0 auto;
      margin-top: 40px;
    }

    .slick-arrow {
      width: 68px;
      height: 0;
      padding-top: 68px;
      overflow: hidden;
      position: absolute;
      z-index: 10;

      @include sp {
        top: 120px;
      }

      @include tabpc {
        top: 50%;
        transform: translateY(-50%);
      }

      &.slick-prev {
        background: url(../images/history/btn_staff_slider_img02.svg) 0 0/68px
          auto no-repeat;
        transition: transform 0.3s;

        @include sp {
          left: 20px;
        }

        @include tabpc {
          left: 0;

          &:hover {
            transform: translateX(-5px) translateY(-50%);
          }
        }
      }

      &.slick-next {
        background: url(../images/history/btn_staff_slider_img01.svg) 0 0/68px
          auto no-repeat;
        transition: transform 0.3s;

        @include sp {
          right: 20px;
        }

        @include tabpc {
          right: 0;

          &:hover {
            transform: translateX(5px) translateY(-50%);
          }
        }
      }
    }

    &-inner {
      &::before {
        content: "";
        position: absolute;
        left: 50%;
        transform: translateX(-50%) scale(0);
        transform-origin: center center;
        top: 0;
        z-index: -1;
        width: 578px;
        height: 536px;
        background: url(../images/history/bg_staff_slider_img01.svg) center 0
          no-repeat;
        transition: transform 0.4s ease;
      }
    }

    .slick-slide {
      @include sp {
        transform: scale(0.85);
      }

      @include tabpc {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        height: 523px;
      }

      .c-module16__slider-meta,
      .c-module16__slider-txt {
        display: none;
      }

      .c-module16__slider-inner {
        background: none;
        padding-bottom: 0;
      }

      .c-module16__slider-pic {
        img {
          display: inline;
          border-radius: 50%;
          background-color: #fff;
          padding: 15px;
        }
      }

      &.slick-active {
        @include sp {
          transform: scale(1);

          .c-module16__slider-inner {
            margin-top: -100px;
          }
        }

        @include tabpc {
          display: block;
          height: inherit;
          transition: all 0.2s;

          & + .slick-slide {
            justify-content: flex-start;
          }

          .c-module16__slider-inner {
            position: relative;
            min-height: 536px;
            transition: all 0.2s;

            &::before {
              content: "";
              transform: translateX(-50%) scale(1);
            }
          }
        }

        .c-module16__slider-meta,
        .c-module16__slider-txt {
          display: block;
        }

        .c-module16__slider-pic {
          img {
            padding: 6px;
            border: 6px solid $thm_color;
          }
        }
      }
    }
  }

  &__slider-head {
    @include sp {
      margin-top: 35px;
    }

    @include tabpc {
      display: flex;
      align-items: flex-end;
      max-width: 490px;
    }
  }

  &__slider-pic {
