  // ↑の編集
  &__modal-link {
    display: block;
    position: relative;
    border-radius: 5px;
    background-color: $thm_color;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      width: 54px;
      height: 54px;
      background: transparent;
      border-top: 1px solid #fff;
      border-left: 1px solid #fff;
      @include sp {
        width: 40px;
        height: 40px;
      }
    }

    &::after {
      position: absolute;
      content: "";
      background-image: url(../images/common/icn_main_arrow_img11.svg);
      background-repeat: no-repeat;
      pointer-events: none;
    }

    @include sp {
      height: 100%;
      padding: 20px 20px 40px 20px;

      &::after {
        width: 22px;
        height: 14px;
        right: 8px;
        bottom: 13px;
        background-size: contain;
        transition: all 0.4s;
      }
    }

    @include tabpc {
      height: 100%;
      padding: 35px 35px 70px 35px;
      &:hover {
        &::after {
          right: 8px;
        }
      }
      &::after {
        width: 26px;
        height: 17px;
        right: 12px;
        bottom: 18px;
        background-size: contain;
        transition: all 0.4s;
      }
    }
  }

  &__number-pic {
    text-align: center;
    transition: all 0.4s;
  }

  &__more {
    text-align: center;

    @include sp {
      margin-top: 50px;
    }

    @include tabpc {
      margin-top: 60px;
    }
  }

  &__number-remodal {
    border-radius: 5px;
    background-color: #f5f8f0;
    color: #171717;

    &::before {
      content: "";
      background-color: #fff;
      width: 100%;
      height: 79%;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: -1;
    }

    @include sp {
      padding: 50px 10px 35px;
    }

    @include tabpc {
      max-width: 685px;
      // max-height: 606px;
      padding: 55px 40px 25px;
    }

    .slick-dots {
      position: absolute;
      text-align: left;
      // max-width: 920px;
      // width: 100%;
      bottom: 0;
      left: 10px;

      @include sptab {
        position: absolute;
        width: 20px;
        margin: 0;
        bottom: -20px;
        right: 20px;
      }

      & > li {
        display: block;
        @include sptab {
          display: block;
          margin: 5px 0;
        }

        & > button {
          &:before {
            position: absolute;
            top: 0;
            left: 0;
            width: 10px;
            height: 10px;
            content: "";
            text-align: center;
            opacity: 1;
            background-color: $border_color01;
            border-radius: 50%;
          }

          &:focus {
            .slick-dots > li > button:before {
              background-color: $cl_green03;
            }
          }
        }

        &.slick-active {
          & > button {
            &:before {
              background-color: $cl_green03;
            }
          }
        }

        &:hover {
          .l-main__index .hero__slider .slick-dots > li > button:before {
            background-color: $cl_green03;
          }
        }
      }
    }
  }

  &__remodal-close {
    position: absolute;
    display: block;
    width: 21px;
    height: 21px;
    z-index: 10;
    cursor: pointer;

    @include sp {
      right: 20px;
      top: 20px;
    }

    @include tabpc {
      right: 30px;
      top: 30px;
    }

    img {
      vertical-align: top;
    }
  }

  &__number-slider {
    @include tabpc {
      padding: 0 70px;
    }
  }

  &__slider-col {
    max-width: 465px;
  }

  &__slider-pic {
    text-align: center;
    @include sp {
      // max-width: 100px;
    }

    img {
      display: inline;
    }
  }

  &__slider-txt {
    text-align: left;

    @include sp {
      margin-top: 45px;
      padding: 0 20px 0 50px;
    }

    @include tabpc {
      margin-top: 40px;
    }
  }
}

