.c-module20 {
  position: relative;
  z-index: 1;

  &__inner {
    max-width: 858px;
    margin: 0 auto;
  }

  &__row {
    position: relative;
    perspective: 858px;
    transform-style: preserve-3d;

    @include sp {
      overflow: hidden;
      padding: 0 30px 40px;

      & + & {
        margin-top: 60px;
      }
    }

    @include tabpc {
      & + & {
        margin-top: 80px;
      }
    }
  }

  &__entry-bverse {
    position: relative;

    @include sp {
      visibility: visible;
      padding: 50px 30px 70px 30px;
    }

    @include tabpc {
      visibility: visible;
      padding: 50px 125px 70px 55px;
    }

    &.is-hide {
      visibility: hidden;
      animation: rotate-1 0.3s ease-in-out forwards;
      z-index: -1;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &.is-show {
      visibility: visible;
      animation: rotate-2 0.3s ease-in-out forwards;
      z-index: 1;
      opacity: 1;
      transition: opacity 0.3s ease-in-out;
    }

    &::before {
      content: "";
      position: absolute;
      left: -20px;
      top: 20px;
      z-index: -1;
      transform: rotate(-3deg);
      transition: all ease 0.3s;
      width: 100%;
      height: 100%;
      background-color: hsl(88, 22%, 87%);
      filter: blur(3px);

      @include sp {
        left: -10px;
        width: calc(100% - 30px);
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 0;
      border-radius: 5px;
      background: $bg_color01;
    }
  }

  &__entry-reverse {
    position: absolute;
    top: 0;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    color: #fff;

    @include sp {
      left: 30px;
      width: calc(100% - 60px);
      padding: 50px 30px 70px 30px;
    }

    @include tabpc {
      left: 0;
      padding: 50px 125px 70px 55px;
    }

    &::before {
      content: "";
      position: absolute;
      left: -20px;
      top: 20px;
      z-index: -1;
      transform: rotate(-3deg);
      transition: all ease 0.3s;
      width: 100%;
      height: 100%;
      background-color: #dee5d6;
      filter: blur(3px);

      @include sp {
        left: -10px;
        width: calc(100% - 30px);
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 0;
      border-radius: 5px;
      background: $thm_color url(../images/project/bg_entry_more_img01.svg) 0 0 /
        cover no-repeat;
    }

    &.is-hide {
      visibility: hidden;
      animation: rotate-1 0.5s ease-in-out forwards;
      z-index: -1;
      opacity: 0;
      transition: opacity 0.3 s ease-in-out;
    }

    &.is-show {
      visibility: visible;
      animation: rotate-2 0s ease-in-out forwards;
      z-index: 1;
      opacity: 1;
      transition: opacity 0.3s ease-in-out;
    }
  }

  @keyframes rotate-1 {
    to {
      transform: rotateY(90deg);
    }
  }

  @keyframes rotate-2 {
    to {
      transform: rotateY(0deg);
    }
  }

  &__entry-more {
    position: absolute;
    z-index: 2;

    @include sp {
      right: -15px;
      bottom: -40px;
      width: 110px;
    }

    @include tabpc {
      right: -25px;
      bottom: -40px;
    }
  }

  &__entry-btn {
    display: block;
  }

  &__entry-ttl {
    position: relative;
    z-index: 2;
    font-weight: bold;

    @include sp {
      font-size: 2.4rem;
    }

    @include tabpc {
      font-size: 2.6rem;
    }
  }

  &__entry-txt {
    position: relative;
    z-index: 2;
    margin-top: 20px;
    font-family: $font-rounded-1c;
  }
}

