      background: url(../images/common/icn_main_arrow_img10.svg) 0 center/12px
        12px no-repeat;
      pointer-events: none;
    }
  }

  &__select-list {
    width: 100%;
    max-width: 200px;
    border: 1px solid #acacac;
    border-radius: 5px;
    background-color: #fff;
    appearance: none;

    &::-ms-expand {
      display: none;
    }

    @include sp {
      padding: 5px 20px;
      font-size: 1.6rem;
    }

    @include tabpc {
      padding: 11px 30px 10px;
      font-size: 1.7rem;
    }
  }

  &__search-txt {
    font-family: $font-rounded-1c;
    font-weight: bold;

    @include sp {
      font-size: 1.8rem;
    }

    @include tabpc {
      font-size: 2rem;
    }
  }
}

.c-module24 {
  &__link-arrow {
    position: absolute;
    bottom: -1px;
    right: -1px;
    z-index: 5;
    display: inline-block;
    border-radius: 0 0 5px 0;

    @include sp {
      width: 48px;
      height: 48px;
    }

    @include tabpc {
      width: 54px;
      height: 54px;
    }

    &.is-white {
      border: 1px solid #fff;
      background: url(../images/common/icn_main_arrow_img11.svg) center center
        no-repeat;
    }

    &.is-yellow {
      transition: all 0.2s;
      border: 1px solid $border_color01;
      background: url(../images/common/icn_main_arrow_img09.svg) center center
        no-repeat;
    }
  }
}

//追加 module11のデザイン変更
.c-module25 {
  position: relative;
  z-index: 1;
  padding: 0 20px;

  @include sp {
    margin-top: 70px;
  }

  @include tabpc {
    margin-top: 135px;
  }

  &__inner {
    max-width: 980px;
    margin: 0 auto;
  }

  &__theme-list {
    position: relative;
    z-index: 1;

    @include tabpc {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &__theme-item {
    border-radius: 5px;
    padding: 30px 20px;
    background-color: #fff;

    @include sp {
      &:not(:first-child) {
        margin-top: 50px;
      }
    }

    @include tabpc {
      width: 44%;

      &:nth-child(n + 3) {
        margin-top: 100px;
      }
    }
  }

  &__theme-wrap {
    position: relative;
  }

  &__theme-pic {
    position: absolute;
    right: 0;
    z-index: 2;

    @include sp {
      width: 65%;
      max-width: 96px;
      top: -65px;
    }

    @include tabpc {
      top: -85px;
    }
  }

  &__theme-detalis {
    @include sp {
      padding-left: 15px;
    }
  }

  &__theme-ttl {
    color: #000;
    font-family: $font-rounded-1c;
    font-size: 2.5rem;
    line-height: 1.6;
    font-weight: bold;
    position: relative;

    @include tabpc {
      margin-top: 15px;
    }

    &::before {
      content: "";
      position: absolute;
      background-color: #2bb350;
      width: 65px;
      height: 2px;
      border-radius: 15px;
      left: 0;
      top: 45px;
    }
  }

  &__theme-txt {
    font-family: $font-rounded-1c;

    @include sp {
      margin-top: 25px;
    }

    @include tabpc {
      margin-top: 30px;
    }
  }
}

.c-module26 {
  position: relative;
  z-index: 1;
  overflow: hidden;

  @include tabpc {
    margin-top: -140px;
    padding-top: 80px;
  }

  &__inner {
    margin-top: 80px;
    display: flex;
    height: 600px;
  }

  &__bg-left {
    width: 45%;
    background: $white;
    display: flex;
    flex-direction: column;
  }

  &__step {
    // position: relative;
    margin: 120px 0 0 0;
    height: calc(100% - 120px);
    display: grid;
    grid-template: " .       .       .       .       step04 " 1fr " .       .       .       step03  step04 " 1fr " .       .       step02  step03  step04 " 1fr " .       step01  step02  step03  step04 " 1fr " fill01  step01  step02  step03  step04 " 1fr " fill02  fill02  fill02  fill02  fill02 " 2fr / 1fr 1fr 1fr 1fr 1fr;
  }

  &__step-item {
    border-top-left-radius: $step-radius;
    background-color: $bg_color01;
    transition: all 0.6s;
    cursor: pointer;
  }

  &__step-item.is-active {
    z-index: 100;

    .c-module26__step-inner {
      border-radius: $step-radius;
      background-color: $thm_color;
      margin-right: -40px;
      color: $white;
      width: calc(100% + 30px);
      height: 200px;
      transition: background-color 0.5s;

      &::after {
        background-color: $white;
        color: $thm_color;
      }
    }
  }

  &__step-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $thm_color;
    font-size: 2.5rem;
    font-weight: bold;
    padding-top: 10px;
    line-height: 1.2;

    &::after {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 60px;
      height: 60px;
      background-color: $thm_color;
      color: $white;
      border-radius: 50%;
      font-weight: bold;
      font-size: 3rem;
    }
  }

  .is-step01 {
    grid-area: step01;

    .c-module26__step-inner::after {
      content: "01";
    }
  }

  .is-step02 {
    grid-area: step02;

    .c-module26__step-inner::after {
      content: "02";
    }
  }

  .is-step03 {
    grid-area: step03;

    .c-module26__step-inner::after {
      content: "03";
    }
  }

  .is-step04 {
    grid-area: step04;

    .c-module26__step-inner::after {
      content: "04";
    }
  }

  &__step-fill01 {
    grid-area: fill01;
    background-color: $bg_color01;
  }

  &__step-fill02 {
    grid-area: fill02;
    background-color: $bg_color01;
  }

  &__bg-right {
    position: relative;
    width: 55%;
    background: $cl_orange02;
    border-top-left-radius: 50px;

    &::after {
      content: "";
      position: absolute;
      top: -180px;
      right: 100px;
      width: 327px;
      height: 231px;
      background-image: url(../images/challenges/img_step_illust.png);
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  &__slider {
    padding: 80px 200px 100px 80px;
    display: flex;
    align-items: center;
    height: 100%;
  }

  &__slider-item {
    position: relative;
    height: 100%;
    border-radius: 20px;
    background-color: $white;
    padding: 80px;
    min-height: 367px;

    .c-module26__step-inner {
      position: absolute;
      top: 5px;
      left: 50px;
    }

    h3 {
      margin-left: 5.4rem;
      font-size: 2rem;
      font-weight: bold;
    }

    p {
      margin-top: 30px;
      font-size: 1.8rem;
    }
  }

