    }

    @include tabpc {
      top: 50%;
      transform: translateY(-50%);
    }

    &.slick-prev {
      background: url(../images/history/btn_staff_slider_img02.svg) 0 0/68px
        auto no-repeat;
      transition: transform 0.3s;

      @include sp {
        left: 20px;
      }

      @include tabpc {
        left: 47px;

        &:hover {
          transform: translateX(-5px) translateY(-50%);
        }
      }
    }

    &.slick-next {
      background: url(../images/history/btn_staff_slider_img01.svg) 0 0/68px
        auto no-repeat;
      transition: transform 0.3s;

      @include sp {
        right: 20px;
      }

