      width: 100%;
      height: 145px;
      object-fit: cover;
      font-family: "object-fit: cover;";
    }

    @include tabpc {
      width: 188px;
      height: 145px;
      object-fit: cover;
      font-family: "object-fit: cover;";
    }
  }

  &__info {
    @include sp {
      width: 100%;
    }

    @include tabpc {
      width: calc(100% - 205px);
    }
  }

  &__title {
    font-weight: bold;
  }

  &__text {
